import React, { Component } from 'react';
import { connect } from 'react-redux';
import M from 'materialize-css';

import Carousel, { CarouselItem } from './Carousel';
import showBoxProject from '../actions/showBoxProject';
import data from '../data/portfolio.json'
import './Modal.css';

class BoxProject extends Component {
    constructor(props) {
        super(props);
        this.state = {
            modalInstance: null,
            projectTitle: undefined,
            logo: undefined,
            description: "Não há",
            client: { name: "", href: "", produtora: "" },
            link: "",
            carouselItems: [],
            stacks: []
        }
        this.mountView = this.mountView.bind(this);
        this.closeModal = this.closeModal.bind(this);
    }

    mountView(index) {
        const project = data[index];
        this.setState({
            projectTitle: project.title,
            logo: project.logo,
            description: project.desc,
            carouselItems: project.images || [],
            link: project.link,
            client: project.client,
            stacks: project.stacks || []
        })

    }

    componentWillReceiveProps(nProp) {
        if (this.state.modalInstance !== null) {
            if (nProp.show === true) {
                this.mountView(nProp.projectId)
                this.state.modalInstance.open()
            } else {
                this.state.modalInstance.close();
            }
        }
    }

    componentDidMount() {
        if (this.state.modalInstance === null) {
            const modal = document.getElementById('ModalBoxProject');
            this.setState({ modalInstance: M.Modal.init(modal) }, () => {
                this.props.show === true ? this.state.modalInstance.open() : this.state.modalInstance.close();
            });
        }
    }
    closeModal() {
        if (this.state.modalInstance !== null) {
            this.props.dispatch(showBoxProject(false))
        }
    }
    getRandomColor() {
        var letters = '0123456789ABCDEF';
        var color = '#';
        for (var i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    }
    render() {
        return (
            <div id="ModalBoxProject" className="modal">
                <button onClick={this.closeModal} className="btn-floating btn-smal waves-effect waves-light  button-close">
                    <i className="material-icons">close</i>
                </button>

                <div className="row info">

                    <div className="col s12 l4 center-align">
                        <img className="logo" src={this.state.logo} alt="Logo" />
                    </div>
                    <div className="col s12 l8 center-align">
                        <h4 className="title">{this.state.projectTitle}</h4>
                    </div>
                    <div className="col s12 l8">
                        <p className="description">{this.state.description}</p>
                    </div>
                    <div className="col s12">
                        <i className="material-icons tiny">person</i> cliente: <a href={this.state.client.href} rel="noopener noreferrer" target="_blank">{this.state.client.name}</a>
                    </div>
                    <div className="col s12">
                        <i className="material-icons tiny">business_center</i> Produtora: {this.state.client.vendor || ""}
                    </div>
                    <div className="col s12">
                        <i className="material-icons tiny">insert_link</i> Link: <a href={this.state.link} rel="noopener noreferrer" target="_blank">{this.state.link}</a>
                    </div>
                </div>
                <div className="row">
                    <div className="col s12 center-align">
                        <Carousel>
                            {this.state.carouselItems.map((url, index) => {
                                return (
                                    <CarouselItem key={index} >
                                        {/* <a href={url}> */}
                                        <img alt="slide" style={{ height: '100%' }} src={url} />
                                        {/* </a> */}
                                    </CarouselItem>
                                )
                            })}
                        </Carousel>
                    </div>
                </div>
                <div className="row">

                    <div className="col s12">
                        Tecnologias:
                    </div>


                    {
                        this.state.stacks.map((tag, index) => {
                            return (
                                <ul>
                                    <li>
                                        <span className="badge" style={{ backgroundColor: this.getRandomColor(), color: "whit" }}>{tag}</span>
                                    </li>
                                </ul>
                            )
                        })
                    }


                </div>
            </div>
        );
    }
}

const mapToProps = (state) => {
    return state.boxproject
}

export default connect(mapToProps)(BoxProject);
