import { OPEN_MODAL, SET_PROJECT_ID } from '../actions/types';

const defaultState = {
    show: false,
    projectId: null
}

export default function boxproject(state = defaultState, action) {
    switch (action.type) {
        case OPEN_MODAL:
            return { ...state, show: action.show };
        case SET_PROJECT_ID:
            return { ...state, projectId: action.projectId }
        default: return state
    }
}
